<template>
  <div class="content">
    <div class="user-info">
      <h2> </h2>
      <p>: {{ userName || "  ." }}</p>
      <p>: {{ email || "  ." }}</p>
      <p>: {{ phone || "  ." }}</p>
    </div>
  </div>
  <!--   -->
  <div class="review-section">
    <h2></h2>
    <div class="review-card-container">
      <div v-if="reviews.length === 0" class="no-reviews">
          .
      </div>
      <div v-for="(review, index) in reviews" :key="index" class="review-card">
        <div class="review-top">
          <h3 class="hotel-name" @click="goToHotel(review.hotelId)">
            {{ review.hotelName }}
          </h3>
          <div class="review-rating">
            <span
              v-for="star in 5"
              :key="star"
              class="star"
              :class="{ filled: star <= review.rating }"
            >
              
            </span>
          </div>
        </div>
        <div class="review-content">{{ review.content }}</div>

        <!--   -->
        <div class="image-gallery">
          <img
            v-for="(image, imgIndex) in review.imageUrl"
            :src="image"
            :key="imgIndex"
            class="thumbnail"
            @click="openLightbox(image)"
          />
        </div>

        <!-- //   -->
        <div class="review-actions">
          <button class="detail-button" @click="openDetailModal(review)">
              
          </button>
          <button v-if="review.content.trim() !== ' '" class="edit-button" @click="openEditModal(review)">
            
          </button>
          <button class="delete-button" @click="confirmDelete(review.reviewId)">
            
          </button>
        </div>
      </div>
    </div>

    <!-- Lightbox -->
    <div v-if="lightboxImage" class="lightbox" @click="closeLightbox">
      <img :src="lightboxImage" alt="Review Image" />
    </div>

    <!--  /  (UserPages  ) -->
    <UserReviewModal
      v-show="isEditModalOpen"
      :isEdit="isEditMode"
      :initialData="selectedReviewData"
      @submit="handleReviewSubmit"
      @close="closeModal"
    />

    <!--     (components  ) -->
    <ReviewModal
      v-show="isDetailModalOpen"
      :isOpen="isDetailModalOpen"
      :review="selectedReviewData"
      @close="closeModal"
    />
  </div>
</template>

<script>
import UserReviewModal from "@/components/UserPages/reviewModal"; //    
import ReviewModal from "@/components/reviewViewModal.vue"; //   
import { updateReview, deleteReview } from "@/api/api";

export default {
  props: {
    userName: {
      type: String,
      required: true,
    },
    email: {
      type: String,
      required: true,
    },
    phone: {
      type: String,
      required: true,
    },
    reviews: Array,
    loggedInUserId: Number,
  },
  components: {
    UserReviewModal,
    ReviewModal,
  },
  data() {
    return {
      isEditModalOpen: false, // /   
      isDetailModalOpen: false, //     
      isEditMode: false, // /  
      selectedReviewData: null, //   
      lightboxImage: null, //  
    };
  },
  methods: {
    openEditModal(review) {
      this.isEditModalOpen = true; // /  
      this.isEditMode = true; //   
      this.selectedReviewData = { ...review }; //    
    },
    openDetailModal(review) {
      this.isDetailModalOpen = true; //    
      this.selectedReviewData = { ...review }; //    
    },
    closeModal() {
      this.isEditModalOpen = false; // /  
      this.isDetailModalOpen = false; //    
      this.isEditMode = false; //   
      this.selectedReviewData = null; //    
    },
    async handleReviewSubmit(reviewData) {
      try {
        const token = sessionStorage.getItem("token");
        if (this.isEditMode && this.selectedReviewData) {
          await updateReview(
            this.selectedReviewData.reviewId,
            reviewData,
            token
          );
          alert(" .");
        }
        this.closeModal();
        this.$emit("update-reviews"); //     
      } catch (error) {
        console.error("    :", error);
        alert("    .");
      }
    },
    async confirmDelete(reviewId) {
      if (confirm("   ?")) {
        try {
          const token = sessionStorage.getItem("token");
          await deleteReview(reviewId, token);
          alert(" .");
          this.$emit("update-reviews"); //     
        } catch (error) {
          console.error("    :", error);
          alert("    .");
        }
      }
    },
    openLightbox(image) {
      this.lightboxImage = image;
    },
    closeLightbox() {
      this.lightboxImage = null;
    },
    goToHotel(hotelId) {
      this.$router.push(`/hotel-details/${hotelId}`);
    },
  },
};
</script>

