<script>
import SidebarLayout from "@/layout/SidebarLayout.vue";
import axios from "axios";

export default {
  name: "HotelAdminPage",
  components: {
    SidebarLayout,
  },
  data() {
    return {
      currentView: "Dashboard", //   
      roomSummary: null, //     
      reservationSummary: [], //     
      rooms: [], //    
      editingRoom: null, //    
      reservations: [], //    
      editingReservation: null, //    
      hotelId: null, //    ID ()

    };
  },
  methods: {
    async fetchHotelId() {
      try {
        //const response = await axios.get("http://localhost:8081/api/manager-hotel-id", { //
        const response = await axios.get("http://43.200.45.122/api/manager-hotel-id", { // 
          
          headers: { Authorization: `Bearer ${sessionStorage.getItem("token")}` },
        });
        this.hotelId = response.data; //  ID 
        console.log("  ID:", this.hotelId);
        // hotelId     
        this.fetchRoomSummary();
        this.fetchRooms();
        this.fetchReservations();
        
      } catch (error) {
        console.error(" ID    :", error);
        alert(" ID  .  .");
      }
    },
    async fetchRoomSummary() {
      try {
        if (!this.hotelId) {
          console.error(" ID  .");
          return;
        }
        //const response = await axios.get(`http://localhost:8081/api/rooms/hotel/${this.hotelId}/room-summary`); //
        const response = await axios.get(`http://43.200.45.122/api/rooms/hotel/${this.hotelId}/room-summary`);
        
        this.roomSummary = response.data;
      } catch (error) {
        console.error("      :", error);
        this.roomSummary = { totalRooms: 0, roomTypeCounts: {} }; //  
      }
    },
    async fetchReservationSummary() {
        try {
             //const response = await axios.get(`http://localhost:8081/api/auth/reservation-summary`, {  //
            const response = await axios.get(`http://43.200.45.122/api/auth/reservation-summary`, { // 
            
                headers: { Authorization: `Bearer ${sessionStorage.getItem("token")}` },
            });
            this.reservationSummary = response.data; //    
        } catch (error) {
            console.error("      :", error);
            this.reservationSummary = [];
        }
    },
    async fetchRooms() {
      try {
        if (!this.hotelId) {
          console.error(" ID  .");
          return;
        }
        //const response = await axios.get(`http://localhost:8081/api/rooms/hotel/${this.hotelId}`); //
        const response = await axios.get(`http://43.200.45.122/api/rooms/hotel/${this.hotelId}`); // 
        this.rooms = response.data;
      } catch (error) {
        console.error("     :", error);
        this.rooms = [];
      }
    },
     //   
    openEditModal(room) {
      this.editingRoom = { ...room }; //      
    },
    //     
    editRoom(room) {
      this.editingRoom = { ...room, amenities: room.amenities || "" }; //  
    },
    //  
    async saveEdit() {
      if (!this.editingRoom.name || !this.editingRoom.occupancy || !this.editingRoom.price) {
        alert("  .");
        return;
      }
      try {
        // PUT     
        await axios.put(
          //`http://localhost:8081/api/rooms/${this.editingRoom.roomId}`, //
          `http://43.200.45.122/api/rooms/${this.editingRoom.roomId}`,
          
          this.editingRoom
        );
        alert("   !");
        this.fetchRooms(); //  
        this.editingRoom = null; //   
      } catch (error) {
        console.error("   :", error);
        alert(" .  .");
      }
    },
    getAmenities(room) {
      const amenities = [];
      if (room.bathFacility) amenities.push("");
      if (room.bath) amenities.push("");
      if (room.airCondition) amenities.push("");
      if (room.tv) amenities.push("TV");
      if (room.cable) amenities.push(" TV");
      if (room.internet) amenities.push("");
      if (room.refrigerator) amenities.push("");
      if (room.toiletries) amenities.push("");
      if (room.sofa) amenities.push("");
      if (room.tableYn) amenities.push("");
      if (room.hairdryer) amenities.push("");

      return amenities.length > 0 ? amenities : [" "];
    },
    getLimitedAmenities(room) {
      if (!room.amenitiesExpanded) {
        return this.getAmenities(room).slice(0, 3); //  3 
      }
      return this.getAmenities(room); //   
    },
    toggleAmenities(room) {
      room.amenitiesExpanded = !room.amenitiesExpanded; // /  
    },
    async updateRoom() {
      try {
        const updatedRoom = {
          ...this.editingRoom,
          amenities: (this.editingRoom.amenities || "").split(",").map((item) => item.trim()),
        };
        //await axios.put(`http://localhost:8081/api/rooms/${updatedRoom.roomId}`, updatedRoom); //
        await axios.put(`http://43.200.45.122/api/rooms/${updatedRoom.roomId}`, updatedRoom); //
        
        alert("  .");
        this.editingRoom = null;
        this.fetchRooms(); //     
      } catch (error) {
        console.error("     :", error);
        alert("   .");
      }
    },
    //   
    async fetchReservations() {
      try {
        if (!this.hotelId) {
          console.error(" ID  .");
          return;
        }
        //const response = await axios.get(`http://localhost:8081/api/auth/reservation-summary?hotelId=${this.hotelId}`, { //
        const response = await axios.get(`http://43.200.45.122/api/auth/reservation-summary?hotelId=${this.hotelId}`, {
          
          headers: { Authorization: `Bearer ${sessionStorage.getItem("token")}` },
        });
        this.reservationSummary = response.data;
      } catch (error) {
        console.error("      :", error);
        this.reservationSummary = [];
      }
    },
    async fetchAllReservations() {
    try {
      //const response = await axios.get("http://localhost:8081/api/auth/all-reservations", { //
      const response = await axios.get("http://43.200.45.122/api/auth/all-reservations", { //
        
        headers: { Authorization: `Bearer ${sessionStorage.getItem("token")}` },
      });
      console.log("Reservations Response:", response.data); //   

      if (Array.isArray(response.data) && response.data.length > 0) {
        this.reservations = response.data;
      } else {
        console.warn("  ."); //    
        this.reservations = [];
      }
    } catch (error) {
      console.error("      :", error.response || error);
      this.reservations = []; //     
    }
  },
  async updateReservation() {
  if (!this.editingReservation) {
    alert("  .");
    return;
  }

  //   
  if (new Date(this.editingReservation.checkIn) >= new Date(this.editingReservation.checkOut)) {
    alert("     .");
    return;
  }

  try {
    //   
    const requestData = {
      status: this.editingReservation.status,
      checkIn: this.editingReservation.checkIn,
      checkOut: this.editingReservation.checkOut,
      request: this.editingReservation.request,
    };

    // PUT 
    const response = await axios.put(
      //`http://localhost:8081/api/auth/reservation/${this.editingReservation.reservationId}`, //
      `http://43.200.45.122/api/auth/reservation/${this.editingReservation.reservationId}`,
      
      requestData,
      {
        headers: { Authorization: `Bearer ${sessionStorage.getItem("token")}` },
      }
    );

    console.log("  :", response.data); //   

    //    
    alert("   !");
    this.editingReservation = null; //   
    this.fetchAllReservations(); //     

  } catch (error) {
    console.error("   :", error);
    alert("    .");
  }
},
  //    
  openEditReservation(reservation) {
    this.editingReservation = { ...reservation }; //      
  },

  cancelEdit(type) {
    if (type === "reservation") {
      this.editingReservation = null; //    
    } else if (type === "room") {
      this.editingRoom = null; //    
    }
  },
  },
  mounted() {
      this.fetchHotelId(); //     ID 
      this.fetchAllReservations();
  },
};
</script>

